import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";

import "./TypeKnowledgeEdit.css";
import { toast } from "react-toastify";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import typeKnowledgeApi from "api/common/typeKnowledge";
import knowledgeApi from "api/common/knowledge";
import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { isErrors } from "utils/utils";

const initValues = {
  // maLoaiKhoiKienThuc: "",
  ten: "",
  khoiKienThuc: 0,
  tongTinChi: 0,
  noidung: "",
};

const TypeKnowledgeEdit = function ({
  values,
  handleChange,
  resetForm,
  errors,
}) {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [listknowledge, setlistknowledge] = useState([
    { id: "", ten: "", maKKT: "" },
  ]);

  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    } else {
      // initValues.maLoaiKhoiKienThuc = "";
      initValues.ten = "";
      initValues.khoiKienThuc = 0;
      initValues.noidung = "";
      initValues.tongTinChi = 0;
      const listKnowledgeRes = await knowledgeApi.getAll();
      if (listKnowledgeRes) {
        setlistknowledge(listKnowledgeRes.contents);
      }
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const listKnowledgeRes1 = await knowledgeApi.getAll();
      if (listKnowledgeRes1) {
        setlistknowledge(listKnowledgeRes1.contents);
      }
      const res = await typeKnowledgeApi.getById(id);
      if (res) {
        // initValues.maLoaiKhoiKienThuc = res.maLoaiKhoiKienThuc;
        initValues.ten = res.ten;
        res.khoiKienThuc?.id ? initValues.khoiKienThuc = res.khoiKienThuc.id : initValues.khoiKienThuc = null;
        initValues.noidung = res.noidung;
        initValues.tongTinChi = res.tongTinChi;
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    }
    setIsLoading(false);
  };

  const handleEditOnClick = async function (e) {
    e.preventDefault();
    let valuesSend = {};
    // valuesSend.maLoaiKhoiKienThuc = values.maLoaiKhoiKienThuc;
    valuesSend.ten = values.ten;
    valuesSend.khoiKienThuc = parseInt(values.khoiKienThuc, 10);
    valuesSend.noidung = values.noidung;
    valuesSend.tongTinChi = values.tongTinChi;
    try {
      if (isUpdatePage) {
        const res = await typeKnowledgeApi.putById(id, valuesSend);
        if (res) {
          toast.success(t("admin.UPDATE_SUCCESS"));
          updateForm(id);
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      } else {
        const res = await typeKnowledgeApi.post(valuesSend);
        if (res) {
          toast.success(t("admin.ADD_SUCCESS"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          }
          else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          }
          else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
    resetForm();
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await typeKnowledgeApi.deleteById(id);
      if (res) {
        toast.success(t("admin.common.DELETED"));
        history.push(RoutesString.ADMIN_TYPE_KNOWLEDGE_LIST)
      } else {
        toast.error(t("RESPONSE_MESSAGE500"));
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.type_knowledge_edit.UPDATE")
            : t("admin.type_knowledge_edit.ADD")
        }
        items={[
          {
            href: RoutesString.ADMIN_TYPE_KNOWLEDGE_LIST,
            title: t("admin.type_knowledge_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          isUpdatePage
            ? t("admin.type_knowledge_edit.UPDATE")
            : t("admin.type_knowledge_edit.ADD")
        }
      />

      <div className="page-content">
        <Form>
          <Notification
            color={notification.color}
            isOpen={notification.isOpen}
            messages={notification.messages}
          />
          {/* <FormGroup className="row">
            <Label for="value" className="col-lg-3">
              {t("admin.type_knowledge_edit.CODE")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaUserAlt />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="maLoaiKhoiKienThuc"
                  placeholder={t("admin.type_knowledge_edit.CODE_PLACEHOLDER")}
                  value={values.maLoaiKhoiKienThuc}
                  onChange={handleChange}
                  invalid={errors.maLoaiKhoiKienThuc}
                  //readOnly={isUpdatePage}
                />
              </InputGroup>
              {errors.maLoaiKhoiKienThuc && (
                <FormText color="danger">{errors.maLoaiKhoiKienThuc}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br /> */}
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.type_knowledge_list.NAME")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ten"
                  placeholder={t("admin.type_knowledge_edit.NAME_PLACEHOLDER")}
                  value={values.ten}
                  onChange={handleChange}
                  invalid={errors.ten}
                />
              </InputGroup>
              {errors.ten && <FormText color="danger">{errors.ten}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.type_knowledge_list.TOTAL_CREDIT")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="tongTinChi"
                  placeholder={t("admin.type_knowledge_edit.NAME_PLACEHOLDER")}
                  value={values.tongTinChi}
                  onChange={handleChange}
                  invalid={errors.tongTinChi}
                />
              </InputGroup>
              {errors.tongTinChi && (
                <FormText color="danger">{errors.tongTinChi}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.type_knowledge_list.CONTENT")}
              {/* <AsteriskIcon /> */}
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="noidung"
                  placeholder={t("admin.type_knowledge_edit.NAME_PLACEHOLDER")}
                  value={values.noidung}
                  onChange={handleChange}
                  invalid={errors.noidung}
                />
              </InputGroup>
              {errors.noidung && (
                <FormText color="danger">{errors.noidung}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.type_knowledge_list.KNOWLEDGE")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="select"
                  name="khoiKienThuc"
                  value={values.khoiKienThuc}
                  onChange={handleChange}
                >
                  <option key={1} value={0}>
                    {t("admin.type_knowledge_edit.KNOWLEDGE_PLACEHOLDER")}
                  </option>
                  {listknowledge.length > 0 &&
                    listknowledge.map((item, index) => {
                      return (
                        <option key={index + 1} value={item.id}>
                          {/* {t("admin.type_knowledge_list.KNOWLEDGE_CODE")}:{" "} */}
                          {item?.chiTietNganh?.khoa}&ensp;{"/"}
                          &ensp;
                          {item?.chiTietNganh?.nganhDaoTao?.ten}&ensp;{"/"}
                          &ensp;
                          {/* {t("admin.type_knowledge_list.KNOWLEDGE_NAME")}:{" "} */}
                          {item.ten}
                        </option>
                      );
                    })}
                </Input>
              </InputGroup>
              {errors.khoiKienThuc && (
                <FormText color="danger">{errors.khoiKienThuc}</FormText>
              )}
            </div>
          </FormGroup>{" "}

          <br />
          <FormGroup>
            <Button color="success" onClick={handleEditOnClick} disabled={isErrors(errors)}>
              {isUpdatePage ? (
                <>
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </>
              ) : (
                <>
                  <FaPlus /> {t("admin.common.ADD")}
                </>
              )}
            </Button>
            &emsp;
            {isUpdatePage && (
              <>
                <Button onClick={handleRefreshOnClick}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
                &emsp;
                <Button color="danger" onClick={handleDeleteOnClick}>
                  <FaTrash /> {t("admin.common.DELETE")}
                </Button>
              </>
            )}
          </FormGroup>
        </Form>
      </div>
    </div>
  );
};

const TypeKnowledgeEditFormikForm = withFormik({
  mapPropsToValues() {
    return initValues;
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    // maLoaiKhoiKienThuc: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")).min(5, i18n.t("admin.type_knowledge_list.NAME_MIN_LENGTH_5")),
    // noidung: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    tongTinChi: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .min(0, i18n.t("admin.POSITIVE_WARNING")),

    khoiKienThuc: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .positive(i18n.t("admin.type_knowledge_list.KNOWLEDGE")),
  }),
})(TypeKnowledgeEdit);

export default TypeKnowledgeEditFormikForm;
